<template>
  <view class="add-product-container">
    <view class="form-container">
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÂêçÁß∞ *</text>
        <input 
          class="form-input" 
          v-model="productForm.name" 
          placeholder="ËØ∑ËæìÂÖ•ÂïÜÂìÅÂêçÁß∞"
          maxlength="50"
        />
      </view>
      
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÂàÜÁ±ª *</text>
        <view class="category-selector">
          <view 
            class="category-option" 
            :class="{ active: categoryIndex === index }"
            v-for="(category, index) in categories" 
            :key="index"
            @click="selectCategory(index)"
          >
            {{ category }}
          </view>
        </view>
        <text class="category-status" v-if="categoryIndex >= 0">
          Â∑≤ÈÄâÊã©: {{ categories[categoryIndex] }}
        </text>
        <text class="category-status" v-else style="color: #999;">
          ËØ∑ÈÄâÊã©‰∏Ä‰∏™ÂàÜÁ±ª
        </text>
      </view>
      
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅ‰ª∑Ê†º *</text>
        <input 
          class="form-input" 
          type="digit" 
          v-model="productForm.price" 
          placeholder="ËØ∑ËæìÂÖ•‰ª∑Ê†º"
        />
      </view>
      
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÊèèËø∞</text>
        <textarea 
          class="form-textarea" 
          v-model="productForm.description" 
          placeholder="ËØ∑ËæìÂÖ•ÂïÜÂìÅÊèèËø∞"
          maxlength="200"
        />
      </view>
      
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÂõæÁâá</text>
        <view class="image-upload-section">
          <view class="image-preview" v-if="productForm.image_url">
            <image :src="productForm.image_url" mode="aspectFill" class="preview-image" />
            <view class="image-actions">
              <button class="image-btn change" @click="chooseImage">Êõ¥Êç¢ÂõæÁâá</button>
              <button class="image-btn delete" @click="removeImage">Âà†Èô§</button>
            </view>
          </view>
          <view class="upload-placeholder" v-else @click="chooseImage">
            <text class="upload-icon">üì∑</text>
            <text class="upload-text">ÁÇπÂáª‰∏ä‰º†ÂõæÁâá</text>
            <text class="upload-tip">ÊîØÊåÅ JPG„ÄÅPNG Ê†ºÂºè</text>
          </view>
        </view>
      </view>
      
      <view class="form-group">
        <text class="form-label">ÂïÜÂìÅÁä∂ÊÄÅ</text>
        <switch 
          :checked="productForm.status" 
          @change="onStatusChange"
          color="#667eea"
        />
        <text class="status-text">{{ productForm.status ? '‰∏äÊû∂' : '‰∏ãÊû∂' }}</text>
      </view>
    </view>
    
    <view class="action-buttons">
      <button class="action-btn cancel" @click="goBack">ÂèñÊ∂à</button>
      <button class="action-btn test" @click="testFormData">ÊµãËØïÊï∞ÊçÆ</button>
      <button class="action-btn submit" @click="submitForm" :disabled="!isFormValid">
        Ê∑ªÂä†ÂïÜÂìÅ
      </button>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      productForm: {
        name: '',
        category: '',
        price: '',
        description: '',
        image_url: '',
        status: true
      },
      categories: [],
      categoryIndex: -1
    };
  },
  onLoad() {
    console.log('ÂïÜÂìÅÊ∑ªÂä†È°µÈù¢Âä†ËΩΩÂÆåÊàê');
    this.loadCategories();
  },
  computed: {
    isFormValid() {
      const valid = this.productForm.name.trim() && 
             this.categoryIndex >= 0 && 
             this.productForm.category && 
             this.productForm.price && 
             !isNaN(parseFloat(this.productForm.price));
      console.log('Ë°®ÂçïÈ™åËØÅÁªìÊûú:', valid, {
        name: this.productForm.name.trim(),
        categoryIndex: this.categoryIndex,
        category: this.productForm.category,
        price: this.productForm.price
      });
      return valid;
    }
  },
  methods: {
    // ÂàÜÁ±ªÈÄâÊã©
    selectCategory(index) {
      console.log('ÈÄâÊã©ÂàÜÁ±ª:', this.categories[index]); // Ê∑ªÂä†Ë∞ÉËØïÊó•Âøó
      this.categoryIndex = index;
      this.productForm.category = this.categories[index];
      console.log('ÂΩìÂâçcategoryIndex:', this.categoryIndex);
      console.log('ÂΩìÂâçÈÄâÊã©ÁöÑÂàÜÁ±ª:', this.productForm.category);
      
      // ÊòæÁ§∫ÈÄâÊã©ÊàêÂäüÊèêÁ§∫
      uni.showToast({ 
        title: `Â∑≤ÈÄâÊã©: ${this.categories[index]}`, 
        icon: 'none',
        duration: 1000
      });
    },
    
    // Áä∂ÊÄÅÂàáÊç¢
    onStatusChange(e) {
      this.productForm.status = e.detail.value;
    },
    
    // Êèê‰∫§Ë°®Âçï
    submitForm() {
      console.log('Êèê‰∫§Ë°®ÂçïÔºåË°®ÂçïÊï∞ÊçÆ:', this.productForm); // Ê∑ªÂä†Ë∞ÉËØïÊó•Âøó
      console.log('Ë°®ÂçïÈ™åËØÅÁªìÊûú:', this.isFormValid); // Ê∑ªÂä†Ë∞ÉËØïÊó•Âøó
      
      // ËØ¶ÁªÜÈ™åËØÅÊèêÁ§∫
      if (!this.productForm.name.trim()) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÂïÜÂìÅÂêçÁß∞', icon: 'none' });
        return;
      }
      
      if (this.categoryIndex < 0) {
        uni.showToast({ title: 'ËØ∑ÈÄâÊã©ÂïÜÂìÅÂàÜÁ±ª', icon: 'none' });
        return;
      }
      
      if (!this.productForm.price || isNaN(parseFloat(this.productForm.price))) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑ‰ª∑Ê†º', icon: 'none' });
        return;
      }
      
      if (!this.isFormValid) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜôÂÆåÊï¥‰ø°ÊÅØ', icon: 'none' });
        return;
      }
      
      uni.showLoading({ title: 'Ê∑ªÂä†‰∏≠...' }); // Ê∑ªÂä†Âä†ËΩΩÊèêÁ§∫
      
      uni.request({
        url: 'http://localhost:3000/api/admin/products',
        method: 'POST',
        header: {
          'Content-Type': 'application/json'
        },
        data: {
          name: this.productForm.name.trim(),
          category: this.productForm.category,
          price: parseFloat(this.productForm.price),
          description: this.productForm.description.trim(),
          image_url: this.productForm.image_url.trim(),
          status: this.productForm.status // Ê∑ªÂä†ÂïÜÂìÅÁä∂ÊÄÅÂà∞ËØ∑Ê±ÇÊï∞ÊçÆ
        },
        success: (res) => {
          uni.hideLoading(); // ÈöêËóèÂä†ËΩΩÊèêÁ§∫
          console.log('APIÂìçÂ∫î:', res); // Ê∑ªÂä†Ë∞ÉËØïÊó•Âøó
          if (res.data && res.data.success) {
            uni.showToast({ title: 'ÂïÜÂìÅÊ∑ªÂä†ÊàêÂäü', icon: 'success' });
            setTimeout(() => {
              uni.navigateBack();
            }, 1500);
          } else {
            uni.showToast({ title: res.data.message || 'Ê∑ªÂä†Â§±Ë¥•', icon: 'none' });
          }
        },
        fail: (err) => {
          uni.hideLoading(); // ÈöêËóèÂä†ËΩΩÊèêÁ§∫
          console.error('ËØ∑Ê±ÇÂ§±Ë¥•:', err); // Ê∑ªÂä†Ë∞ÉËØïÊó•Âøó
          uni.showToast({ title: 'ÁΩëÁªúÈîôËØØÔºåËØ∑Ê£ÄÊü•ÂêéÁ´ØÊúçÂä°ÊòØÂê¶ÂêØÂä®', icon: 'none' });
        }
      });
    },
    
    // ËøîÂõû‰∏ä‰∏ÄÈ°µ
    goBack() {
      uni.navigateBack();
    },

    // ÊµãËØïÊï∞ÊçÆ
    testFormData() {
      console.log('ËÆæÁΩÆÊµãËØïÊï∞ÊçÆ...');
      this.productForm.name = 'ÊµãËØïÂïÜÂìÅÂêçÁß∞';
      this.productForm.category = 'ÁúüÈ≤úÊûúËå∂';
      this.productForm.price = '15.00';
      this.productForm.description = 'ËøôÊòØ‰∏ÄÊù°ÊµãËØïÂïÜÂìÅÊèèËø∞„ÄÇ';
      this.productForm.image_url = 'https://via.placeholder.com/150';
      this.productForm.status = true;
      this.categoryIndex = 1; // ÈÄâÊã©ÁúüÈ≤úÊûúËå∂
      console.log('ÊµãËØïÊï∞ÊçÆÂ∑≤ËÆæÁΩÆ:', this.productForm);
      console.log('categoryIndex:', this.categoryIndex);
      console.log('Ë°®ÂçïÈ™åËØÅÁªìÊûú:', this.isFormValid);
      uni.showToast({ title: 'ÊµãËØïÊï∞ÊçÆÂ∑≤ËÆæÁΩÆ', icon: 'success' });
    },

    // ÈÄâÊã©ÂõæÁâá
    chooseImage() {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: (res) => {
          const filePath = res.tempFilePaths[0];
          this.uploadImage(filePath);
        },
        fail: () => {
          uni.showToast({ title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•', icon: 'none' });
        }
      });
    },

    // ‰∏ä‰º†ÂõæÁâá
    uploadImage(filePath) {
      uni.showLoading({ title: '‰∏ä‰º†‰∏≠...' });
      
      uni.uploadFile({
        url: 'http://localhost:3000/api/admin/upload-product-image',
        filePath: filePath,
        name: 'file',
        success: (uploadRes) => {
          uni.hideLoading();
          const data = JSON.parse(uploadRes.data);
          if (data.success) {
            this.productForm.image_url = data.url;
            uni.showToast({ title: '‰∏ä‰º†ÊàêÂäü', icon: 'success' });
          } else {
            uni.showToast({ title: data.message || '‰∏ä‰º†Â§±Ë¥•', icon: 'none' });
          }
        },
        fail: () => {
          uni.hideLoading();
          uni.showToast({ title: '‰∏ä‰º†Â§±Ë¥•', icon: 'none' });
        }
      });
    },

    // Âà†Èô§ÂõæÁâá
    removeImage() {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÂº†ÂõæÁâáÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            this.productForm.image_url = '';
            uni.showToast({ title: 'ÂõæÁâáÂ∑≤Âà†Èô§', icon: 'success' });
          }
        }
      });
    },

    // Âä†ËΩΩÂàÜÁ±ª
    async loadCategories() {
      try {
        const res = await uni.request({
          url: 'http://localhost:3000/api/admin/categories',
          method: 'GET',
          header: { 'Content-Type': 'application/json' }
        });
        if (res.data && res.data.success) {
          this.categories = res.data.categories.map(item => item.name);
          console.log('ÊàêÂäüÂä†ËΩΩÂàÜÁ±ª:', this.categories);
        } else {
          uni.showToast({ title: res.data.message || 'Âä†ËΩΩÂàÜÁ±ªÂ§±Ë¥•', icon: 'none' });
          console.error('Âä†ËΩΩÂàÜÁ±ªÂ§±Ë¥•:', res.data.message);
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂàÜÁ±ªÂ§±Ë¥•:', error);
        uni.showToast({ title: 'ÁΩëÁªúÈîôËØØÔºåÊó†Ê≥ïÂä†ËΩΩÂàÜÁ±ª', icon: 'none' });
      }
    }
  }
};
</script>

<style scoped>
.add-product-container {
  min-height: 100vh;
  background: #f5f5f5;
  padding: 20rpx;
}

.form-container {
  background: #fff;
  border-radius: 16rpx;
  padding: 30rpx;
  margin-bottom: 40rpx;
  box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 30rpx;
}

.form-label {
  display: block;
  font-size: 28rpx;
  color: #333;
  margin-bottom: 16rpx;
  font-weight: 500;
}

.form-input {
  width: 100%;
  height: 80rpx;
  border: 2rpx solid #e5e5e5;
  border-radius: 12rpx;
  padding: 0 20rpx;
  font-size: 28rpx;
  background: #f8f9fa;
  box-sizing: border-box;
}

.form-input:focus {
  border-color: #667eea;
  background: #fff;
}

.category-selector {
  display: flex;
  flex-wrap: wrap;
  gap: 15rpx;
  padding: 10rpx 0;
}

.category-option {
  background-color: #f8f9fa;
  color: #333;
  padding: 16rpx 24rpx;
  border-radius: 25rpx;
  font-size: 28rpx;
  border: 2rpx solid #e5e5e5;
  white-space: nowrap;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  min-width: 120rpx;
}

.category-option:active {
  transform: scale(0.95);
}

.category-option.active {
  background-color: #667eea;
  color: #fff;
  border-color: #667eea;
  box-shadow: 0 4rpx 12rpx rgba(102, 126, 234, 0.3);
}

.category-status {
  font-size: 24rpx;
  color: #667eea;
  margin-top: 10rpx;
  display: block;
}

.image-upload-section {
  width: 100%;
}

.image-preview {
  position: relative;
  width: 100%;
  height: 300rpx;
  border-radius: 12rpx;
  overflow: hidden;
  background: #f8f9fa;
  border: 2rpx solid #e5e5e5;
}

.preview-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-actions {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  padding: 20rpx;
  display: flex;
  gap: 20rpx;
}

.image-btn {
  flex: 1;
  height: 60rpx;
  border-radius: 8rpx;
  font-size: 24rpx;
  border: none;
}

.image-btn.change {
  background: #667eea;
  color: #fff;
}

.image-btn.delete {
  background: #dc3545;
  color: #fff;
}

.upload-placeholder {
  width: 100%;
  height: 300rpx;
  border: 2rpx dashed #e5e5e5;
  border-radius: 12rpx;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #f8f9fa;
  cursor: pointer;
  transition: all 0.3s ease;
}

.upload-placeholder:active {
  background: #e9ecef;
  border-color: #667eea;
}

.upload-icon {
  font-size: 60rpx;
  margin-bottom: 20rpx;
}

.upload-text {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 10rpx;
}

.upload-tip {
  font-size: 24rpx;
  color: #999;
}

.form-textarea {
  width: 100%;
  height: 120rpx;
  border: 2rpx solid #e5e5e5;
  border-radius: 12rpx;
  padding: 20rpx;
  font-size: 28rpx;
  background: #f8f9fa;
  box-sizing: border-box;
  resize: none;
}

.form-textarea:focus {
  border-color: #667eea;
  background: #fff;
}

.form-group:last-child {
  display: flex;
  align-items: center;
  gap: 20rpx;
}

.status-text {
  font-size: 28rpx;
  color: #666;
}

.action-buttons {
  display: flex;
  gap: 20rpx;
}

.action-btn {
  flex: 1;
  height: 88rpx;
  border-radius: 12rpx;
  font-size: 32rpx;
  font-weight: 500;
  border: none;
}

.action-btn.cancel {
  background: #fff;
  color: #666;
  border: 2rpx solid #e5e5e5;
}

.action-btn.test {
  background: #ff9800;
  color: #fff;
  border: 2rpx solid #ff9800;
}

.action-btn.submit {
  background: #667eea;
  color: #fff;
}

.action-btn.submit:disabled { background: #ccc; }
</style> 